<template>
  <div v-theme:column="'wide'" id="show-blogs">
    <div class="message-title">
      <h2>全部留言({{filteredBlogs.length}})</h2>
      <input type="text" v-model="search" placeholder="搜索留言标题" />
    </div>
    <div class="single-blog" v-for="(blog,i) in filteredBlogs" :key="i">
      <router-link v-bind:to="'/blog/'+blog.id">
        <h3 v-rainbow title="留言详情">{{blog.title | to-uppercase}}</h3>

        <article title="留言内容">
          {{blog.content | snippet}}
          <span
            v-rainbow
            class="categories"
            title="留言类型"
          >{{blog.categories}}</span>
          <span class="time">来自 {{blog.author}} 的留言 {{blog.time}}</span>
        </article>
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from "../axios-auth"; //引用引入当前axios-auth.js
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    axios
      .get("/posts.json")
      .then(function(data) {
        return data.data;
      })
      .then(data => {
        var blogsArray = [];
        for (let key in data) {
          data[key].id = key;
          blogsArray.unshift(data[key]);
          this.blogs = blogsArray;
        }
      });
  },
  computed: {
    filteredBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  filters: {
    toUppercase(value) {
      return value.toUpperCase();
    }
  },
  // 指令全部变成本地
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    }
  }
};
</script>

<style >
a {
  color: red;
}
h2 {
  color: #333;
}
h3 {
  padding-bottom: 10px;
}
#show-blogs {
  max-width: 800px;
  border-radius: 10px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  position: relative;
  border-radius: 8px;
}
#show-blogs a {
  color: #444;
  text-decoration: none;
}
.message-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
article {
  font-size: 14px;
  text-indent: 2em;
  margin-bottom: 10px;
}
.time {
  font-size: 12px;
  position: absolute;
  bottom: 5px;
  right: 15px;
}
.categories {
  font-size: 12px;
  position: absolute;
  top: -10px;
  right: 15px;
}
input[type="text"] {
  padding: 8px;
  box-sizing: border-box;
  background: none;
  border: 1.5px solid #b6b6b6;
  font-size: 13px;
  width: 200px;
  height: 35px;
}
</style>



