<template>
  <div id="add-blog">
    <h2>编辑留言</h2>
    <form v-if="!submmited">
      <label>编辑留言标题 :</label>
      <input type="text" v-model="blog.title" required />
      <label>编辑留言内容 :</label>
      <textarea v-model="blog.content"></textarea>
      <label for>编辑留言类型 :</label>
      <div id="checkboxs">
        <input type="radio" id="beckoning" name="check" value="怦然心动" v-model="blog.categories" />
        <label for="beckoning">怦然心动</label>
        <input type="radio" id="communication" name="check" value="情感交流" v-model="blog.categories" />
        <label for="communication">情感交流</label>
        <input type="radio" id="interaction" name="check" value="技术互动" v-model="blog.categories" />
        <label for="interaction">技术互动</label>
        <input type="radio" id="atale" name="check" value="秋之物语" v-model="blog.categories" />
        <label for="atale">秋之物语</label>
        <input type="radio" id="locust" name="check" value="指桑骂槐" v-model="blog.categories" />
        <label for="locust">指桑骂槐</label>
        <input type="radio" id="other" name="check" value="隶属其它" v-model="blog.categories" />
        <label for="other">隶属其它</label>
      </div>
      <label for>编辑作者关系 :</label>
      <select name v-model="blog.author">
        <option v-for="author in authors" :key="author">{{author}}</option>
      </select>
    </form>
    <div v-if="submmited">
      <h3>编辑留言成功!</h3>
    </div>
    <div id="preview">
      <h3>留言预览</h3>
      <p>
        <b>留言标题 :</b>
        {{blog.title}}
      </p>

      <p>
        <b>留言内容 :</b>
        {{blog.content}}
      </p>

      <p>
        <b>留言类型 :</b>
        {{blog.categories}}
      </p>

      <p>
        <b>作者关系 :</b>
        {{blog.author}}
      </p>
      <button @click.prevent="post" v-if="!submmited">确认编辑</button>
    </div>
  </div>
</template>

<script>
import axios from "axios"; //引用axios
export default {
  name: "add-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
      authors: [
        "亲人",
        "恋人",
        "同学",
        "朋友",
        "闺蜜",
        "死党",
        "同事",
        "陌生人"
      ],
      submmited: false
    };
  },
  methods: {
    fetchDate() {
      axios.get("/posts/" + this.id + ".json").then(response => {
        this.blog = response.data;
      });
    },
    post: function() {
      var isFlag = confirm("你确认编辑吗?");
      if (isFlag) {
        var admin = prompt("请输入超管秘钥:", "");
        if (admin === "admin") {
          axios.put("/posts/" + this.id + ".json", this.blog).then(data => {
            this.submmited = true;
          });
        } else {
          alert("秘钥错误,请联系管理员QQ:452566060");
        }
      }
    }
  },
  created() {
    this.fetchDate();
  }
};
</script>


<style scoped>
#add-blog * {
  box-sizing: border-box;
}
#add-blog {
  margin: 20px auto;
  max-width: 1200px;
  background: #6677cc33;
  padding: 20px;
  border-radius: 10px;
}
label {
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
  font-size: 14px;
}
textarea {
  height: 200px;
  resize: none;
  font-family: "微软雅黑";
  font-size: 16px;
}
#checkboxs label {
  display: inline-block;
  margin-top: 0;
  margin-right: 20px;
  cursor: pointer;
}
#checkboxs input {
  display: inline-block;
  margin-right: 2px;
  margin-top: 0;
}
button {
  display: block;
  margin: 20px auto;
  background: crimson;
  color: 0;
  padding: 14px;
  width: 120px;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
#preview p {
  margin-bottom: 10px;
  font-family: "微软雅黑";
}

h3 {
  margin-top: 10px;
  text-align: center;
}
</style>



