<template>
  <div id="single-blog">
    <p>
      <b>留言标题 :</b>
      {{blog.title}}
    </p>
    <p>
      <b>留言内容 :</b>
      {{blog.content}}
    </p>
    <p>
      <b>留言类型 :</b>
      {{blog.categories}}
    </p>
    <p>
      <b>作者关系 :</b>
      {{blog.author}}
    </p>
    <div class="single-button">
      <button @click="deleteSingleBlog()">删除</button>
      <router-link :to="'/edit/'+id">编辑</router-link>
    </div>
  </div>
</template>
<script>
import axios from "axios"; //引用axios
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  created() {
    axios.get("/posts/" + this.id + ".json").then(data => {
      this.blog = data.data;
    });
  },
  methods: {
    deleteSingleBlog: function() {
      var admin = prompt("请输入超管秘钥:", "");
      if (admin === "admin") {
        axios.delete("/posts/" + this.id + ".json").then(response => {
          this.$router.push({ path: "/" });
        });
        alert("删除成功");
      } else {
        alert("秘钥错误,请联系管理员QQ:452566060");
      }
    }
  }
};
</script>
<style  scoped>
#single-blog {
  margin: 20px auto;
  max-width: 1200px;
  background: #6677cc33;
  padding: 20px;
  border-radius: 10px;
}
#single-blog p {
  margin-bottom: 10px;
}
.single-button {
  display: flex;
  justify-content: center;
  align-items: center;
}
a,
button {
  text-align: center;
  display: block;
  margin: 20px auto;
  background: rgb(181, 25, 26);
  color: #fff;
  padding: 8px 22px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
}
a {
  background: rgb(85, 210, 246);
  text-decoration: none;
  color: #fff;
}
</style>


